body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.arc * {
  box-sizing: border-box;
}

.arc {
  margin: 2rem;
}

.arc {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 276px;
  height: 276px;
  overflow: hidden;
  border: 1px solid #efeff1;
  border-radius: 8px;
}

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/** Header **/

.arc_header {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px;
  height: 14.6%;
}

.arc_header_nav {
  flex: 0 0 28px;
  box-shadow: 0 0px 4px 2px #f1f1f1;
  border-radius: 6px;
  cursor: pointer;
  width: 28px;
  height: 28px;
}

.arc_header_label {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 12px;
  box-shadow: 0 0px 4px 2px #f1f1f1;
  border-radius: 6px;
  cursor: pointer;
  padding: 5px;
}

.arc_header_label span {
  font-weight: 500;
  font-size: 14px;
}

.arc_header_label > div + div {
  margin-left: 8px;
}

.arc_header_label-days-of-month {
}

.arc_header_label-months {
}

.arc_header_label-years {
}

/** Body Content **/

.arc_view {
  height: calc(100% - 14.6%);
  width: 100%;
}

/** Months View **/

.arc_view-months {
  height: 100%;
}

.arc_view-months .arc_view_row {
  display: flex;
  width: 100%;
  height: 25%;
}

.arc_view-months .arc_view_cell {
  flex-basis: 33.33%;
  max-width: 33.33%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.arc_view-months .arc_view_cell button {
  width: 78px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
}

.arc_view-months .arc_view_cell.arc_selected {
  box-shadow: 0px 0px 1px 1px #0c57c5;
  background-color: #5599ff;
  border-radius: 6px;
  color: #fff;
  font-weight: 500;
}

.arc_view-months .arc_view_cell.arc_this_month {
  border-radius: 6px;
  font-weight: 500;
  color: #448fff;
}

/** Years View **/

.arc_view-years {
  height: 100%;
  width: 100%;
}

.arc_view-years .arc_view_row {
  display: flex;
  width: 100%;
  height: 25%;
}

.arc_view-years .arc_view_cell {
  flex-basis: 20%;
  max-width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.arc_view-years .arc_view_cell button {
  width: 48px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  cursor: pointer;
}

.arc_view-years .arc_view_cell.arc_this_year {
  border-radius: 6px;
  font-weight: 500;
  color: #448fff;
}

/** Days of Month View - Weekdays Header Row **/

.arc_view_weekdays {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 100%;
  border-bottom: 1px solid #f7f7f7;
  margin-bottom: 9px;
}

.arc_view_weekdays_cell {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 14.285%;
  max-width: 14.285%;
  height: 32px;
  color: #b6b6b7;
  font-size: 13px;
  text-transform: uppercase;
}

.arc_view_weekdays_cell.arc_wknd {
  color: #f3a0a0;
}

.arc_view_weekdays_cell + .arc_view_weekdays_cell {
  margin-left: 6px;
}

/** Days of Month View **/

.arc_view-days-of-month {
  height: calc(100% - 42px);
}

.arc_view-days-of-month .arc_view_row {
  display: flex;
  width: 100%;
  height: 16.66%;
  align-items: center;
}

.arc_view-days-of-month .arc_view_cell {
  flex-basis: 14.285%;
  max-width: 14.285%;
  height: 100%;
  padding: 2px 0;
}

.arc_view-days-of-month .arc_view_cell + .arc_view_cell {
  margin-left: 6px;
}

.arc_view-days-of-month .arc_view_cell .arc_view_cell_value {
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.arc_view-days-of-month .arc_view_cell button {
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  cursor: pointer;
  font-size: 13px;
  color: #b6b6b7;
}

.arc_view-days-of-month .arc_view_cell.arc_active button {
  color: #000;
  font-weight: 500;
}

.arc_view-days-of-month .arc_view_cell.arc_wknd button {
  color: red;
}

.arc_view-days-of-month .arc_view_cell.arc_wknd:not(.arc_active) button {
  color: #f3a0a0;
}

.arc_view-days-of-month .arc_view_cell.arc_in_range:not(.arc_disabled) .arc_view_cell_value {
  background-color: #f1f6ff;
}

.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_wknd:not(.arc_disabled) .arc_view_cell_value {
  background-color: #fff1f3;
}

.arc_view-days-of-month .arc_view_cell.arc_in_range:not(.arc_active) .arc_view_cell_value {
  background-color: #fff;
}

.arc_view-days-of-month .arc_view_cell.arc_in_range.arc_wknd:not(.arc_active) .arc_view_cell_value {
  background-color: #fff;
}

.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_active .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_active .arc_view_cell_value {
  background-color: #448fff;
}

.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_active button,
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_active button {
  color: #fff;
}

.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_wknd.arc_active .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_wknd.arc_active .arc_view_cell_value {
  background-color: red;
}

.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_wknd.arc_active button,
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_wknd.arc_active button {
  color: #fff;
}

.arc_view-days-of-month .arc_view_cell.arc_range_start:not(.arc_active) .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_end:not(.arc_active) .arc_view_cell_value {
  /* background-color: #fff; */
}

.arc_view-days-of-month .arc_view_cell.arc_range_start.arc_wknd:not(.arc_active) .arc_view_cell_value,
.arc_view-days-of-month .arc_view_cell.arc_range_end.arc_wknd:not(.arc_active) .arc_view_cell_value {
  /* background-color: #fff; */
}

.arc_view-days-of-month .arc_view_cell.arc_today button {
  font-weight: 500;
  color: #448fff;
}

.arc_view-days-of-month .arc_view_cell.arc_selected .arc_view_cell_value {
  background-color: #5599ff;
}

.arc_view-days-of-month .arc_view_cell.arc_selected:not(.arc_active) .arc_view_cell_value {
  background-color: #ecf3ff;
}

.arc_view-days-of-month .arc_view_cell.arc_selected .arc_view_cell_value button {
  color: #fff;
  font-weight: 500;
}

.arc_view-days-of-month .arc_view_cell.arc_selected:not(.arc_active) .arc_view_cell_value button {
  color: #c2d7f9;
}

.arc_view-days-of-month .arc_view_cell.arc_selected button:focus {
  outline: none;
}

.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd .arc_view_cell_value {
  background-color: red;
}

.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd:not(.arc_active) .arc_view_cell_value {
  background-color: #ffecec;
}

.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd .arc_view_cell_value button {
  color: #fff;
  font-weight: 500;
}

.arc_view-days-of-month .arc_view_cell.arc_selected.arc_wknd:not(.arc_active) .arc_view_cell_value button {
  color: #f9c2c2;
}

.arc_view-days-of-month .arc_view_cell.arc_disabled {
  position: relative;
}

.arc_view-days-of-month .arc_view_cell.arc_disabled button {
  color: #c9c9ca;
  cursor: none;
  pointer-events: none;
}

.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_wknd button {
  color: #f5b5b5;
}

.arc_view-days-of-month .arc_view_cell.arc_disabled button::before {
  content: '';
  position: absolute;
  transform: rotate(45deg);
  font-weight: 300;
  display: block;
  height: 70%;
  background-color: #b6b6b7;
  width: 1px;
}

.arc_view-days-of-month .arc_view_cell.arc_disabled.arc_wknd button::before {
  background-color: #f5b5b5;
}

.arc_view-days-of-month .arc_view_cell.arc_wknd button::before {
  background-color: #f5b5b5;
}

.arc_view-days-of-month .arc_view_cell.arc_wknd.arc_disabled::before {
  background-color: #f3a0a0;
}
